.form {
  display: flex;
  width: 100%;
  padding: 30px 20px;
  background: gray;
  border-radius: 10px;
  margin-bottom: 30px;
  flex-wrap: nowrap;
  align-items: flex-end;
  justify-content: space-between;
}
.left {
  display: flex;
  column-gap: 20px;
}

.label {
  display: flex;
  flex-direction: column;
}

.title {
  font-size: 20px;
  text-align: center;
  font-weight: 600;
  color: #081588;
  margin-bottom: 3px;
}

.input {
  height: 30px;
  border-radius: 5px;
  border: none;
}

.btn {
  padding: 10px 20px;
  white-space: nowrap;
  border-radius: 20px;
  margin-left: 10px;
  font-weight: 600;
  color: #081588;
}
